import Vue from 'vue'
// import Router from 'vue-router'
// import Login from './components/login.vue'
const Login = () => import(/* webpackChunkName:"Login_Home_Welcome" */ './components/login.vue')
// import Home from './components/home.vue'
const Home = () => import(/* webpackChunkName:"Login_Home_Welcome" */ './components/home.vue')
// import Welcome from './components/welcome.vue'
const Welcome = () => import(/* webpackChunkName:"Login_Home_Welcome" */ './components/welcome.vue')
// import User from './components/users/user.vue'
const User = () => import(/* webpackChunkName:"User_Rights_Roles" */ './components/users/user.vue')
// import Rights from './components/authorization/rights.vue'
const Rights = () => import(/* webpackChunkName:"User_Rights_Roles" */ './components/authorization/rights.vue')
// import Roles from './components/authorization/roles.vue'
const Roles = () => import(/* webpackChunkName:"User_Rights_Roles" */ './components/authorization/roles.vue')
// import Cate from './components/goods/cate.vue'
const Cate = () => import(/* webpackChunkName:"Cate_Params_Goods" */ './components/goods/cate.vue')
// import Params from './components/goods/params.vue'
const Params = () => import(/* webpackChunkName:"Cate_Params_Goods" */ './components/goods/params.vue')
// import Goods from './components/goods/goodsList.vue'
const Goods = () => import(/* webpackChunkName:"Cate_Params_Goods" */ './components/goods/goodsList.vue')
// import Add from './components/goods/add.vue'
const Add = () => import(/* webpackChunkName:"Add" */ './components/goods/add.vue')
// import Order from './components/Order.vue'
const Order = () => import(/* webpackChunkName:"Order_Report" */ './components/Order.vue')
// import Report from './components/report.vue'
const Report = () => import(/* webpackChunkName:"Order_Report" */ './components/report.vue')

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/home',
      component: Home,
      redirect: '/welcome',
      children: [
        {
          path: '/welcome',
          component: Welcome
        },
        {
          path: '/users',
          component: User
        },
        {
          path: '/roles',
          component: Roles
        },
        {
          path: '/rights',
          component: Rights
        },
        {
          path: '/categories',
          component: Cate
        },
        {
          path: '/params',
          component: Params
        },
        {
          path: '/goods',
          component: Goods
        },
        {
          path: '/goods/add',
          component: Add
        },
        {
          path: '/orders',
          component: Order
        },
        {
          path: '/reports',
          component: Report
        }
      ]
    }
  ]
})
router.beforeEach((to, from, next) => {
  if (to.path === '/login') return next()
  const tokenStr = sessionStorage.getItem('token')
  if (!tokenStr) return next('/login')
  next()
})
export default router
